.image_banner {
  position: relative;
  picture {
    display: block;
    z-index: 1;
    position: relative;
  }
  img {
    width: 100%;
    display: block;
    aspect-ratio: 1 / 1;
    object-fit: cover;
    @media screen and (min-width: 880px) {
      aspect-ratio: 512 / 301;
    }
    @media screen and (min-width: 1200px) {
      aspect-ratio: 84 / 41;
    }
    @media screen and (min-width: 1680px) {
      aspect-ratio: 64 / 25;
    }
  }
  .cta {
    position: absolute;
    left: 0;
    right: 0;
    top: 90px;
    bottom: 0;
    z-index: 2;
    .text_box {
      max-width: 800px;
      flex: 1 0 800px;
    }
  }
  .text, .buttons {
    margin: 16px;
  }
  .text {
    &+.buttons {
      margin-top: 32px;
    }
  }
}